import SuggestionComment from "./suggestion-comment";
import ContentEditable from 'react-contenteditable';
import icons from '../component/icons';
const { Fragment } = wp.element;
const { __ } = wp.i18n;  // eslint-disable-line
const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
const { Button, ResponsiveWrapper } = wp.components;
const { MediaUpload, MediaUploadCheck } = wp.blockEditor;
const $ = jQuery; // eslint-disable-line

export default class SuggestionBoard extends React.Component {

  constructor(props) {

    super(props);
    this.contentEditable = React.createRef();
    this.displayComments = this.displayComments.bind(this);
    this.updateComment = this.updateComment.bind(this);
    this.removeComment = this.removeComment.bind(this);
    this.addNewComment = this.addNewComment.bind(this);
    this.cancelComment = this.cancelComment.bind(this);
    this.removeAttachment = this.removeAttachment.bind(this);

    this.state = { suggestedOnText: this.props.suggestedOnText, newcommentText: '' };

  }

  removeComment(index) {
    const { suggestionID } = this.props;
    const timestamp = getTimestampWithTimezone();
    let currentBoardSuggestion = this.state.suggestedOnText;
    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
    suggestionHistory = JSON.parse(suggestionHistory);
    currentBoardSuggestion[index]['status'] = 'deleted';
    suggestionHistory[suggestionID] = currentBoardSuggestion;
    suggestionHistory[suggestionID][0]['is_draft'] = false;
    suggestionHistory[suggestionID][0]['updated_at'] = timestamp;
    wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
    //currentBoardSuggestion.splice(index, 1); 
    this.setState({ suggestedOnText: currentBoardSuggestion });
    wp.data.dispatch('core/editor').autosave();
  }
  removeAttachment(index) {
    const { suggestionID } = this.props;
    let currentBoardSuggestion = this.state.suggestedOnText;
    currentBoardSuggestion[index]['attachmentText'] = '';
    this.setState({ suggestedOnText: currentBoardSuggestion });
    jQuery(`#sg${suggestionID}`).find(".btn-disabled").addClass('btn-success');
    jQuery(`#sg${suggestionID}`).find(".btn-success").removeClass('btn-disabled');
    wp.data.dispatch('core/editor').autosave();
  }

  updateComment(newText, index) {

    const { suggestionID } = this.props;
    var attachmentText;
    const timestamp = getTimestampWithTimezone();
    let currentBoardSuggestion = this.state.suggestedOnText;
    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
    suggestionHistory = JSON.parse(suggestionHistory);
    //if attachment already exist
    if (undefined !== currentBoardSuggestion[index]['attachmentText'] && '' !== currentBoardSuggestion[index]['attachmentText']) {
      attachmentText = currentBoardSuggestion[index]['attachmentText'];
    }
    else {

      attachmentText = jQuery('#cf_thumb_file').html();
    }

    // Replace content with filter HTML(without HTML tags) which we get from AJAX response. Github issue: #491. @author: Rishi Shah @since: 3.5
    var newTextstring = '';
    var data = {
      'action': 'cf_suggestion_text_filter',
      'newText': newText,
    };

    var _this = this;
    $.post(ajaxurl, data, function (data) { // eslint-disable-line
      data = $.parseJSON(data);
      newTextstring = data.arr;

      currentBoardSuggestion[index]['text'] = newTextstring;
      currentBoardSuggestion[index]['editedTime'] = timestamp;
      currentBoardSuggestion[index]['attachmentText'] = attachmentText;
      suggestionHistory[suggestionID] = currentBoardSuggestion;
      suggestionHistory[suggestionID][0]['is_draft'] = false;
      suggestionHistory[suggestionID][0]['updated_at'] = timestamp;
      wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
      _this.setState({ suggestedOnText: currentBoardSuggestion });
    });
      
  }

  addNewComment(event) {
    event.preventDefault();
    const { suggestionID } = this.props;
    let currentTextID = 'txt' + suggestionID;
    var newText = jQuery('#' + currentTextID).html();
    newText = filterTextBeforeSave(newText);
    var attachmentText = jQuery('#cf_thumb_file').html();
    //let newTextstring = validateCommentReplyText(newText);

    let newTextstring = '';
    // Replace content with filter HTML(without HTML tags) which we get from AJAX response. Github issue: #491. @author: Rishi Shah @since: 3.5
    var data = {
      'action': 'cf_suggestion_text_filter',
      'newText': newText,
    };

    var _this = this;
    $.post(ajaxurl, data, function (data) { // eslint-disable-line
      data = $.parseJSON(data);
      newTextstring = data.arr;
      if ('' !== newText && '' !== newTextstring && /\S/g.test(newTextstring)) {
        const currentUser = getCurrentUserId();
        const timestamp = getTimestampWithTimezone();
        let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
        suggestionHistory = JSON.parse(suggestionHistory);
        let newCommentInfo = { 'uid': currentUser, 'action': 'reply', 'mode': 'Reply', 'text': newTextstring, 'is_draft': true, 'timestamp': timestamp, 'status': 'publish', 'attachmentText': attachmentText };
        suggestionHistory[suggestionID].push(newCommentInfo);
        suggestionHistory[suggestionID][0]['is_draft'] = true;
        suggestionHistory[suggestionID][0]['updated_at'] = timestamp;
        wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
        jQuery('#' + currentTextID).html('').focus();
        _this.setState({ suggestedOnText: suggestionHistory[suggestionID], newcommentText: '', mediaId: '', mediaName: '', mediaUrl: '' });
  
      } else {
        if (jQuery(event.currentTarget).hasClass('btn-disabled')) {
          return false;
        }
        var noticeMsg =  __('Please add some comment. ', 'content-collaboration-inline-commenting');
        document.getElementById("cf-board__notice").innerHTML = noticeMsg;
        document.getElementById("cf-board__notice").setAttribute('style','display:block');
        setTimeout(function(){
            document.getElementById("cf-board__notice").setAttribute('style','display:none');
            document.getElementById("cf-board__notice").innerHTML = "";
        }, 3000);
      }
  
      if (jQuery(`#sg${suggestionID} .cf-assign-to`).length) {
        jQuery(`#sg${suggestionID} .cf-assign-to`).remove();
      }
      jQuery(`#sg${suggestionID}`).find(".btn-success").addClass('btn-disabled');
      wp.data.dispatch('core/editor').autosave();
    });

  }

  cancelComment() {
    const { suggestionID } = this.props;
    let currentTextID = 'txt' + suggestionID;
    jQuery('#' + currentTextID).html('');
    jQuery('#cf_thumb_file').html('');
    // Reset Comments Float.
    jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
    jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
    jQuery('[data-rich-text-format-boundary]').removeAttr('data-rich-text-format-boundary');

    // Removing the active class form activity center on cancel.
    jQuery('.js-activity-centre .user-data-row').removeClass('active');
    // Fixed ggithib issue #31
    this.setState({ newcommentText: '', mediaId: '', mediaName: '', mediaUrl: '' });
    jQuery.event.trigger({ type: "showHideComments" });
  }

  deleteAttachment() {
    this.setState({ attachmentText: '', mediaId: '', mediaName: '', mediaUrl: '' })
    jQuery('#cf_thumb_file').html('');
  }

  displayComments(data, i) {
    const { suggestionID } = this.props;
    var status = data.status ? data.status : '';
    if (status === 'deleted') {
      return;
    }
    return (
      <SuggestionComment
        key={i}
        index={i}
        removeCommentFromBoard={this.removeComment}
        removeAttachmentFromBoard={this.removeAttachment}
        updateCommentFromBoard={this.updateComment}
        dateTime={data.time}
        status={status}
        timestamp={data.timestamp}
        userID={data.uid}
        action={data.action}
        suggestionID={suggestionID}
        clientId={this.props.clientId}
        mode={data.mode}
        title={data.text}
        editedTime={data.editedTime}
        attachmentText={data.attachmentText}
      >
        {'reply' === data.action &&
          data.text
        }
        {'reply' !== data.action &&
          <Fragment>
            <strong>{data.action}: </strong>
            {data.text.length > 100 ? data.text.substring(0, 100) + '...' : data.text}
          </Fragment>
        }
      </SuggestionComment>
    );

  }

  render() {
    const { suggestionID } = this.props;
    let commentCount = this.state.suggestedOnText.length;
    let commentToggleVisibility = (commentCount > getCommentsLimit()) ? 'block' : 'none';

    var newText = this.state.newcommentText;
    newText = validateCommentReplyText(newText);

    let buttonClass = (newText === '') ? 'btn-disabled' : '';

    return (
      <div className={"board sg-board " + this.state.suggestedOnText[0].action}>
        <div className="boardTop">
          {
            this.state.suggestedOnText && this.state.suggestedOnText.map((item, index) => {
              return this.displayComments(item, index);
            })
          }
          <div className="show-all-comments" style={{ display: commentToggleVisibility }} > {sprintf(__('Show all %d replies'), (commentCount - 1))} </div>
        </div>
        <React.Fragment>
              {"guest" !== currentUserData.role &&
                <div className="shareCommentContainer">
                    <div className="cf-share-comment-wrapper js-cf-share-comment-wrapper">
                      <ContentEditable
                        innerRef={this.contentEditable}
                        html={this.state.newcommentText}
                        disabled={false}
                        onChange={(e) => this.setState({ newcommentText: e.target.value })}
                        id={"txt" + suggestionID}
                        className="cf-share-comment js-cf-share-comment"
                        placeholder={__('Reply or add others with @', 'content-collaboration-inline-commenting')}
                      />

                    </div>

                    <div className="btn-wrapper">
                      <button onClick={this.addNewComment} className={`btn btn-success ${buttonClass}`}>{__('Reply', 'content-collaboration-inline-commenting')}<span className="tooltip">{__('Reply to comment', 'content-collaboration-inline-commenting')}</span></button>
                      <button onClick={this.cancelComment} className="btn btn-cancel">{__('Cancel', 'content-collaboration-inline-commenting')} <span className="tooltip">{__('Discard Comment', 'content-collaboration-inline-commenting')}</span></button>
                    </div>
                </div>
              } 
        </React.Fragment>  
      </div>
    );
  }
}