import renderHTML from 'react-render-html';
import ContentEditable from 'react-contenteditable';
import _merge from "lodash.merge";
import icons from '../component/icons';
import React from 'react';

let addedClass = 'mdadded', modifiedClass = 'mdmodified', removedClass = 'mdremoved';
const $ = jQuery; // eslint-disable-line
const { __ } = wp.i18n;  // eslint-disable-line
const { MediaUpload, MediaUploadCheck } = wp.blockEditor;// eslint-disable-line
const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

export default class SuggestionComment extends React.Component {

  constructor(props) {

    super(props);

    this.contentEditable = React.createRef();
    this.editComment = this.editComment.bind(this);
    this.saveComment = this.saveComment.bind(this);
    this.removeComment = this.removeComment.bind(this);
    this.cancelEdit = this.cancelEdit.bind(this);
    this.removeSuggestion = this.removeSuggestion.bind(this);
    this.acceptSuggestion = this.acceptSuggestion.bind(this);
    this.acceptAllSuggestion = this.acceptAllSuggestion.bind(this);
    this.copy = this.copy.bind(this);
    this.updateBlockContentChanges = this.updateBlockContentChanges.bind(this);
    this.state = { editing: false, contentHtml: '<br/>' };

    /**dynamic user data */
    this.state = {
      currentUserid: getCurrentUserId(),
      isLoaded: false,
    };

  }

  editComment() {
    const { suggestionID } = this.props;
    this.setState({ editing: true });
    // Handling edited value.
    var editedValue = this.props.children[0];
    editedValue = removeLinkFromEditableText(editedValue);
    this.state.contentHtml = editedValue;
    if ('' !== this.props.attachmentText) {
      this.setState({ mediaId: '', mediaName: '', mediaUrl: '' });
    }
  }

  saveComment(event) {

    var previousValue = this.props.children[0];
    const { suggestionID } = this.props;
    var attachment = this.props.attachmentText;
    var index = this.props.index;
    // Filtering anchor tag and return the url text only.
    // For bug fixing of semicolon there is a little chnage in regex
    previousValue = removeLinkFromEditableText(previousValue);

    var newText = this.state.contentHtml;
    if (jQuery(event.currentTarget).hasClass('btn-disabled')) {
      return false;
    }
    newText = filterTextBeforeSave(newText);

    let newTextstring = newText.replace(/<br>/igm, '');
    let newTextstringFlag = /\S/g.test(newTextstring);

    if (!newTextstringFlag) {
      var noticeMsg = __('Please add some comment. ', 'content-collaboration-inline-commenting');
      document.getElementById("cf-board__notice").innerHTML = noticeMsg;
      document.getElementById("cf-board__notice").setAttribute('style', 'display:block');
      setTimeout(function () {
        document.getElementById("cf-board__notice").setAttribute('style', 'display:none');
        document.getElementById("cf-board__notice").innerHTML = "";
      }, 3000);
      return false;
    }

    if (newText === previousValue && "" === newText) {
      var noticeMsg = __('Please edit your comment.', 'content-collaboration-inline-commenting');
      document.getElementById("cf-board__notice").innerHTML = noticeMsg;
      document.getElementById("cf-board__notice").setAttribute('style', 'display:block');
      setTimeout(function () {
        document.getElementById("cf-board__notice").setAttribute('style', 'display:none');
        document.getElementById("cf-board__notice").innerHTML = "";
      }, 3000);

      return false;
    }

    if ('' === attachment) {
      let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
      suggestionHistory = JSON.parse(suggestionHistory);
      suggestionHistory[suggestionID][index]['attachmentText'] = '';
      wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });

    }

    this.props.updateCommentFromBoard(newText, this.props.index);
    this.setState({ editing: false })
  }
  removeComment(event) {

    this.props.removeCommentFromBoard(this.props.index);
    jQuery(".comment-delete-overlay").removeClass("show");

  }
  copy(event) {

    var elID = jQuery(event.currentTarget).closest('.cls-board-outer');
    elID = elID[0].id;
    const elIDRemove = elID;
    var current_url = window.location.href + '&current_url=' + elIDRemove;

    jQuery('.copytext').text('');
    jQuery('#' + elIDRemove).find('.copytext').text(current_url);
    //hack for safari = add style='position: absolute; top: -8888px; left: -8888px'
    var $temp = jQuery("<input style='position: absolute; top: -8888px; left: -8888px'>");
    var $url = current_url;
    jQuery("body").append($temp);
    $temp.val($url).select();
    document.execCommand("copy");
    event.target.focus();
    $temp.remove();
    this.setState({ copySuccess: __('Link Copied!', 'content-collaboration-inline-commenting') });
    clearInterval(this.resetState());

    // Create an auxiliary hidden input
    var aux = document.createElement("input");
    // Get the text from the element passed into the input
    //aux.setAttribute("value", document.getElementById('text_element').innerHTML);
    aux.select();
  }
  resetState() {
    setTimeout(() => this.setState({ copySuccess: '' }), 3000);
  }
  cancelEdit() {
    const { suggestionID } = this.props;
    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
    let currentBoardSuggestion = this.state.attachmentText;
    let index = this.props.index;
    suggestionHistory = JSON.parse(suggestionHistory);
    this.props.attachmentText = suggestionHistory[suggestionID][index]['attachmentText'];
    this.setState({ editing: false, attachmentText: currentBoardSuggestion });
  }
  deleteAttachment(event) {
    const elID = jQuery(event.currentTarget).closest('.cls-board-outer');
    this.setState({ attachmentText: '', mediaId: '', mediaUrl: '', mediaName: '' })
    this.props.removeAttachmentFromBoard(this.props.index);
  }
  rejectAllSuggestion() {
    var allContent = wp.data.select('core/editor').getEditedPostAttribute('content');
    let allElements = document.querySelectorAll('del,ins,[class~="alignupdate"],[class~="headingupdate"]');
    const userId = wp.data.select('core').getCurrentUser().id;
    const timestamp = getTimestampWithTimezone();

    let content;
    if ('' !== allContent) {
      let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
      if (0 < suggestionHistory.length) {
        suggestionHistory = JSON.parse(suggestionHistory);
        for (let i = 0; i < allElements.length; i++) {
          let suggestionID = allElements[i].id;
          let findItem = 'id="' + suggestionID + '"';
          if (suggestionHistory[suggestionID] && -1 !== allContent.indexOf(findItem)) {

            let suggestionDetail = suggestionHistory[suggestionID][0];
            let mode = suggestionHistory[suggestionID][0].mode;
            let action = suggestionHistory[suggestionID][0].action;
            let tempDiv = document.createElement('div');
            let childElements;
            let clientId = jQuery('#' + allElements[i].id).parents('.wp-block')[0].getAttribute('data-block');
            let blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);

            content = blockAttributes.content;
            if (undefined !== blockAttributes.content) {
              content = blockAttributes.content;
            } else if (undefined !== blockAttributes.caption) {
              content = blockAttributes.caption;
            } else {
              content = blockAttributes.values;
            }
            tempDiv.innerHTML = content;  // phpcs:ignore

            if ('add' === mode.toLowerCase()) {
              childElements = tempDiv.getElementsByClassName(addedClass);
            } else if ('edit' === mode.toLowerCase()) {
              childElements = tempDiv.getElementsByClassName(modifiedClass);
            } else {
              childElements = tempDiv.getElementsByClassName(removedClass);
            }

            for (let j = 0; j < childElements.length; j++) {
              if (undefined !== childElements[j].id && suggestionID === childElements[j].id) {

                if (suggestionDetail['is_block_align']) {
                  childElements[j].parentNode.removeChild(childElements[j]);
                  if (suggestionDetail['oldvalue'] !== undefined || suggestionDetail['oldvalue'] !== '') {
                    if (blockAttributes.textAlign) {
                      wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { textAlign: suggestionDetail['oldvalue'] } });
                    } else {
                      wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { align: suggestionDetail['oldvalue'] } });
                    }
                  } else {
                    if (blockAttributes.textAlign) {
                      wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { textAlign: '' } });
                    } else {
                      wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { align: '' } });
                    }

                  }
                }
                //  Add heading level suggestion/@author Pooja Bhimani/@since 2.0.4.5 
                if (suggestionDetail['is_block_level']) {
                  childElements[j].parentNode.removeChild(childElements[j]);
                  if (suggestionDetail['oldvalue'] !== undefined || suggestionDetail['oldvalue'] !== '') {
                    wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { level: suggestionDetail['oldvalue'] } });
                  } else {
                    wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { level: '' } });
                  }
                }

                if ('add' === mode.toLowerCase() || 'edit' === mode.toLowerCase()) {
                  if ('format' === action.toLowerCase() || 'link' === action.toLowerCase()) {
                    let currentElement = jQuery('#' + suggestionID, tempDiv)[0];
                    if (suggestionDetail['is_element'] && suggestionDetail['oldvalue'] !== undefined && !suggestionDetail['is_attr_update']) {
                      jQuery('#' + suggestionID, tempDiv).replaceWith(suggestionDetail['oldvalue']);  // phpcs:ignore
                    } else if (suggestionDetail['is_attr_update'] && suggestionDetail['attrs_old'] && suggestionDetail['attrs_new']) {

                      if (currentElement.nodeName.toLowerCase() === 'ins' ||
                        currentElement.nodeName.toLowerCase() === 'del'
                      ) {
                        currentElement = currentElement.firstChild;
                      }

                      for (const [key, value] of Object.entries(suggestionDetail['attrs_new'])) {
                        currentElement.removeAttribute(key);
                      }

                      for (const [key, value] of Object.entries(suggestionDetail['attrs_old'])) {
                        currentElement.setAttribute(key, value);
                      }
                      jQuery('#' + suggestionID, tempDiv).replaceWith(currentElement.outerHTML);  // phpcs:ignore                
                    }
                  } else {
                    childElements[j].parentNode.removeChild(childElements[j]);
                  }
                } else if ('delete' === mode.toLowerCase() && (suggestionDetail['is_block_remove'] || suggestionDetail['is_block_align'] || suggestionDetail['is_block_level'])) {
                  jQuery('#' + suggestionID, tempDiv).remove();
                } else {
                  jQuery('#' + suggestionID, tempDiv).replaceWith(childElements[j].innerHTML);  // phpcs:ignore
                }

                // save status for activity center log
                suggestionHistory[suggestionID][0]['status'] = { 'action': 'reject', 'user': userId, 'timestamp': timestamp };
                suggestionHistory[suggestionID][0]['is_draft'] = true;
                suggestionHistory[suggestionID][0]['updated_at'] = timestamp;

                let finalContent = tempDiv.innerHTML;
                this.updateBlockContentChanges(clientId);

                if (undefined !== blockAttributes.content) {
                  wp.data.dispatch('core/block-editor').updateBlock(clientId, {
                    attributes: {
                      content: finalContent
                    }
                  });
                } else if (undefined !== blockAttributes.caption) {
                  wp.data.dispatch('core/block-editor').updateBlock(clientId, {
                    attributes: {
                      caption: finalContent
                    }
                  });
                } else {
                  wp.data.dispatch('core/block-editor').updateBlock(clientId, {
                    attributes: {
                      values: finalContent
                    }
                  });
                }
              }
            }
            if(null !== document.getElementById('sg' + suggestionID)){
              document.getElementById('sg' + suggestionID)?.remove();
              jQuery(`#cf-sg${suggestionID}`).removeClass('active');
            }

          }
        }
        wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
        this.updatePostMeta();
        jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
        jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
        jQuery('#cf-span__comments .cls-board-outer').removeClass('is-open');
        //break;

      }
    }
    jQuery.event.trigger({ type: "onBlockLevelSuggestionUpdate" });
    wp.data.dispatch('core/editor').autosave();

  }
  // Updated remove suggestion with the latest code to resolve #500 @uthor - Mayank/since3.5
  removeSuggestion() {

    const { suggestionID } = this.props;
    let { clientId } = this.props;
    const userId = wp.data.select('core').getCurrentUser().id;
    const timestamp = getTimestampWithTimezone();
    let blockName = wp.data.select('core/block-editor').getSelectedBlock()?.name;
    let content;
    const findAttributes = window.adminLocalizer.allowed_attribute_tags;
    let blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    if (blockAttributes === null && suggestionID !== null) {
      clientId = jQuery('#' + suggestionID).parents('.wp-block')[0]?.getAttribute('data-block');
      if(!clientId) {
        clientId = jQuery('[suggestion_id="' + suggestionID + '"], [align_sg_id="' + suggestionID + '"], [textAlign_sg_id="' + suggestionID + '"], [lock_sg_id="' + suggestionID + '"]').attr('data-block');
      }
      blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    }
    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;

    // For add block level suggestion @author Mayank Jain
    if (0 < suggestionHistory.length) {
      suggestionHistory = JSON.parse(suggestionHistory);
      let suggestionDetail = suggestionHistory[suggestionID][0];
      let blockAlignSuggestion = false, blockTextAlignSuggestion = false, blockLockSuggestion = false;

      if(suggestionDetail['is_block_align'] && blockAttributes?.align_sg_id){
        blockAlignSuggestion = true;
      }
      if(suggestionDetail['is_text_align'] && blockAttributes?.textAlign_sg_id){
        blockTextAlignSuggestion = true;
      }
      if(suggestionDetail['is_block_lock'] && blockAttributes?.lock_sg_id){
        blockLockSuggestion = true;
      }

      if(suggestionDetail['is_block_added'] || suggestionDetail['is_block_remove'] || blockAlignSuggestion || blockTextAlignSuggestion || blockLockSuggestion) {
        let secondaryClientId =  jQuery('[suggestion_id="' + suggestionID + '"], [align_sg_id="' + suggestionID + '"], [textAlign_sg_id="' + suggestionID + '"], [lock_sg_id="' + suggestionID + '"]').attr('data-block');
        let selectedBlockId = clientId;
        if(secondaryClientId !== clientId){
          selectedBlockId = secondaryClientId;
        }
        if(suggestionDetail['is_block_added']) {
          jQuery('[suggestion_id="' + suggestionID + '"]').addClass("blockSuggestion");
          const attributes = wp.data.select('core/block-editor').getBlockAttributes(selectedBlockId);
          attributes.className = 'blockSuggestion mdadded blockAdded';

          // Delete all the comments if block deleted @Author - Mayank
          var commentsIds = $('#block-'+selectedBlockId + ', #block-'+selectedBlockId + ' *').map(function() {
            return $(this).attr('datatext');
          }).get();
          if(commentsIds){
            commentsIds.forEach(function(commentsId) {
              const matchingIds = $(`[datatext=${commentsId}]`);
              if (matchingIds) {
                $('#'+commentsId).remove(); // remove the element with the sid
              }
            }); 
          }

          wp.data.dispatch('core/block-editor').updateBlockAttributes(selectedBlockId, attributes);
          wp.data.dispatch('core/block-editor').removeBlock(selectedBlockId);
          
          const parentElement = document.getElementById('block-'+selectedBlockId);
          if(parentElement){
            const childElements = parentElement.querySelectorAll('ins, del');
            if(childElements){
              const childIds = Array.from(childElements).map(element => 'sg' + element.getAttribute('id'));
              if(childIds){
                childIds.forEach(childId => {
                  const element = document.getElementById(childId);
                  if (element) {
                    element.remove();
                  }
                });
              }
            }
          }
          
        }
        if(suggestionDetail['is_block_remove']){
          const element = document.querySelector(`[suggestion_id="${suggestionID}"]`);
          if(element){
            element.classList.remove("mdremoved", "blockremove");
            element.removeAttribute("suggestion_id");
            element.removeAttribute("data-sgremove");
            
            const attributes = wp.data.select('core/block-editor').getBlockAttributes(selectedBlockId);
            attributes.className = attributes.className.replace(/(mdremoved|blockremove)\s*/g, ''); // Removed block remove class @author - Mayank / since 3.5

            wp.data.dispatch('core/editor').updateBlockAttributes(selectedBlockId, {
              className: attributes.className,
              suggestion_id: null,
            });
            const hideSuggestionIcon = document.querySelectorAll('.cf-icon-wholeblock__comment');
            if(hideSuggestionIcon){
              hideSuggestionIcon.forEach(element => {
                if (!element.hasAttribute('datatext')) {
                  element.style.display = 'none';
                }
                element.classList.remove('focus');
              });
            }
          }
        }

        if(blockAlignSuggestion){ // For block align suggestion media blocks. @author - Mayank / since 3.5
          let alignValue = suggestionDetail['oldvalue'] || '';
          wp.data.dispatch('core/block-editor').updateBlockAttributes(selectedBlockId, {
            align: alignValue,
            align_sg_id: null,
          });
        }
        if(blockTextAlignSuggestion){ // For block textalign suggestion media blocks. @author - Mayank / since 3.5
          let textAlignValue = suggestionDetail['oldvalue'] || '';
          wp.data.dispatch('core/block-editor').updateBlockAttributes(selectedBlockId, {
            textAlign: textAlignValue,
            textAlign_sg_id: null,
          });
        }
        if(blockLockSuggestion){ // For block lock suggestion media blocks. @author - Mayank / since 3.5
          wp.data.dispatch('core/block-editor').updateBlockAttributes(selectedBlockId, {
            lock: suggestionDetail['oldvalue'],
            lock_sg_id: null,
          });
        }

        suggestionHistory[suggestionID][0]['status'] = { 'action': 'reject', 'user': userId, 'timestamp': timestamp };
        suggestionHistory[suggestionID][0]['is_draft'] = true;
        suggestionHistory[suggestionID][0]['updated_at'] = timestamp;    

        this.updateBlockContentChanges(selectedBlockId);

        if(document.getElementById('sg' + suggestionID)){
          document.getElementById('sg' + suggestionID)?.remove();
        }
        wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
        this.updatePostMeta();
        wp.data.dispatch('core/editor').savePost(); // To resolve #493 issue @author - Mayank / since 3.5
        jQuery(`#cf-sg${suggestionID}`).removeClass('active');
        jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
        jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
        jQuery('#cf-span__comments .cls-board-outer').removeClass('is-open');

        return;
      }
    }
    if(blockAttributes){
      for (const [key, value] of Object.entries(blockAttributes)) {
        if(findAttributes.includes(key)){
          content = blockAttributes[key];
          if ('' !== content) {
            let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
            if (0 < suggestionHistory.length) {
              suggestionHistory = JSON.parse(suggestionHistory);
              let findItem = 'id="' + suggestionID + '"';
              if (suggestionHistory[suggestionID] && -1 !== content.indexOf(findItem)) {
                let suggestionDetail = suggestionHistory[suggestionID][0];
                let mode = suggestionHistory[suggestionID][0].mode;
                let action = suggestionHistory[suggestionID][0].action;
                let tempDiv = document.createElement('div');
                let childElements;
                tempDiv.innerHTML = content;  // phpcs:ignore 
                if ('add' === mode.toLowerCase()) {
                  childElements = tempDiv.getElementsByClassName(addedClass);
                } else if ('edit' === mode.toLowerCase()) {
                  childElements = tempDiv.getElementsByClassName(modifiedClass);
                } else {
                  childElements = tempDiv.getElementsByClassName(removedClass);
                }

                
                for (let i = 0; i < childElements.length; i++) {
                  if (undefined !== childElements[i].id && suggestionID === childElements[i].id) {

                    // Remove inside delete tag if exists and remove the sg board. @author: Rishi Shah. @since: 3.6
                    var childDelSuggesionId = jQuery('#' + suggestionID).find('.mdremoved').attr( 'id' );
                    if( '' !== childDelSuggesionId ) {
                      jQuery('#sg' + childDelSuggesionId).remove();
                    }

                    // remove comment board over suggestion if we reject the suggestion. @author: Rishi Shah.
                    var childCommentId = jQuery('#' + suggestionID).find('.mdspan-comment').attr( 'datatext' );
                    var childSuggesionId = jQuery('#' + suggestionID).find('.mdmodified').attr( 'id' );
                    if( childElements[i].classList.contains('mdadded') ) {
                      setTimeout(function () { 
                        if( '' !== childCommentId && undefined !== childCommentId ) {
                          removeTag( childCommentId ); // eslint-disable-line
                          jQuery('#' + childCommentId).remove();
                        } else if( '' !== childSuggesionId ) {
                          jQuery('#sg' + childSuggesionId).remove();
                        }
                      }, 500);
                    }
                    
                    if( undefined !== suggestionDetail['is_block_lock'] ) { // Lock feture is handled separately. @author - Mayank/ since 3.5
                      wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { lock: suggestionDetail['oldvalue'] });
                      childElements[i].parentNode.removeChild(childElements[i]);
                    }

                    if (suggestionDetail['is_block_align']) {
                      if(childElements[i]){ // Prevent from warning if element doesn't exists #514 @author - Mayank / since 3.5
                        childElements[i].parentNode.removeChild(childElements[i]);
                      }
                      if (suggestionDetail['oldvalue'] !== undefined || suggestionDetail['oldvalue'] !== '') {
                        if ('core/heading' === blockName) {
                          wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, {  textAlign: suggestionDetail['oldvalue'] } ); // Changed updateBlock to updateBlockAttribute. @author - Mayank/ since 3.5
                        } else {
                          wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { align: suggestionDetail['oldvalue'] }); // Changed updateBlock to updateBlockAttribute. @author - Mayank/ since 3.5
                        }
                      } else {
                        if ('core/heading' === blockName) {
                          wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { textAlign: '' } ); // Changed updateBlock to updateBlockAttribute. @author - Mayank/ since 3.5
                        } else {
                          wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { align: '' } ); // Changed updateBlock to updateBlockAttribute. @author - Mayank/ since 3.5
                        }
                      }
                    }

                    if(suggestionDetail['is_text_align']){ // Added for text align functionality @author - Mayank / since 3.5
                      if (suggestionDetail['oldvalue'] !== undefined || suggestionDetail['oldvalue'] !== '') {
                        wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { textAlign: suggestionDetail['oldvalue']});
                      } else {
                        wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { textAlign: ''});
                      }
                    }

                    /**
                     * Add heading level suggestion
                     *
                     * @author Pooja Bhimani
                     * @since 3.0.1 
                     */
                    if (suggestionDetail['is_block_level']) {
                      childElements[i].parentNode.removeChild(childElements[i]);
                      if (suggestionDetail['oldvalue'] !== undefined || suggestionDetail['oldvalue'] !== '') {
                        wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { level: suggestionDetail['oldvalue'] } });
                      } else {
                        wp.data.dispatch('core/block-editor').updateBlock(clientId, { attributes: { level: '' } });
                      }
                    }
      
                    if ('add' === mode.toLowerCase() || 'edit' === mode.toLowerCase()) {
                      
                      if ('format' === action.toLowerCase() || 'link' === action.toLowerCase()) {
                        let currentElement = jQuery('#' + suggestionID, tempDiv)[0];
                        if (suggestionDetail['is_element'] && suggestionDetail['oldvalue'] !== undefined && !suggestionDetail['is_attr_update'] && null === currentElement.innerHTML.match(/<mdspan/g) ) {
                          jQuery('#' + suggestionID, tempDiv).replaceWith(suggestionDetail['oldvalue']);  // phpcs:ignore
                        } else if( undefined !== currentElement && null !== currentElement?.innerHTML.match(/<mdspan/g) ) {
                          jQuery('#' + suggestionID, tempDiv).replaceWith(currentElement.firstChild.innerHTML);
                        } else if (suggestionDetail['is_attr_update'] && suggestionDetail['attrs_old'] && suggestionDetail['attrs_new']) {
      
                          if (currentElement.nodeName.toLowerCase() === 'ins' ||
                            currentElement.nodeName.toLowerCase() === 'del'
                          ) {
                            currentElement = currentElement.firstChild;
                          }
      
                          for (const [key, value] of Object.entries(suggestionDetail['attrs_new'])) {
                            currentElement.removeAttribute(key);
                          }
      
                          for (const [key, value] of Object.entries(suggestionDetail['attrs_old'])) {
                            currentElement.setAttribute(key, value);
                          }
                          jQuery('#' + suggestionID, tempDiv).replaceWith(currentElement.outerHTML);  // phpcs:ignore                
                        }
                      } else {
                        childElements[i].parentNode.removeChild(childElements[i]);
                      }
                    } else if ('delete' === mode.toLowerCase() && (suggestionDetail['is_block_remove'] || suggestionDetail['is_block_align'] || suggestionDetail['is_block_level'])) {
                      jQuery('#' + suggestionID, tempDiv).remove();
                    } else {
                      jQuery('#' + suggestionID, tempDiv).replaceWith(childElements[i].innerHTML);  // phpcs:ignore
                    }
      
                    // save status for activity center log
                    suggestionHistory[suggestionID][0]['status'] = { 'action': 'reject', 'user': userId, 'timestamp': timestamp };
                    suggestionHistory[suggestionID][0]['is_draft'] = true;
                    suggestionHistory[suggestionID][0]['updated_at'] = timestamp;    
                    let finalContent = tempDiv.innerHTML;
                    this.updateBlockContentChanges(clientId);
                    wp.data.dispatch('core/block-editor').updateBlock(clientId, {
                      attributes: {
                        [key]: finalContent
                      }
                    });
                    document.getElementById('sg' + suggestionID)?.remove();
                    wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
                    this.updatePostMeta();
                    wp.data.dispatch('core/editor').savePost(); // To resolve #493 issue @author - Mayank / since 3.5
                    jQuery(`#cf-sg${suggestionID}`).removeClass('active');
                    jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
                    jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
                    jQuery('#cf-span__comments .cls-board-outer').removeClass('is-open');
                    break;
                  }
                }

              }
            }
          }
        }
      }
    }

   
    jQuery.event.trigger({ type: "onBlockLevelSuggestionUpdate" });
    wp.data.dispatch('core/editor').autosave();
  }

  updateBlockContentChanges(clientId) {
    if ('' !== clientId) {
      let updateBlockChanges = wp.data.select('core/editor').getEditedPostAttribute('meta')['_sb_update_block_changes'];

      if (0 < updateBlockChanges.length) {
        updateBlockChanges = JSON.parse(updateBlockChanges);
      }

      if (0 === updateBlockChanges.length) {
        updateBlockChanges = {};
        updateBlockChanges[clientId] = true;
      } else if (!updateBlockChanges[clientId]) {
        updateBlockChanges[clientId] = true;
      }
      wp.data.dispatch('core/editor').editPost({ meta: { _sb_update_block_changes: JSON.stringify(updateBlockChanges) } });
      wp.data.dispatch('core/editor').autosave();
    }
  }
  acceptAllSuggestion() {
    var allContent = wp.data.select('core/editor').getEditedPostAttribute('content');
    let allElements = document.querySelectorAll('.is-root-container del,.is-root-container ins,[class~="alignupdate"],[class~="headingupdate"]');

    const userId = wp.data.select('core').getCurrentUser().id;
    const timestamp = getTimestampWithTimezone();
    let tempDiv = document.createElement('div');
    tempDiv.innerHTML = allContent;  // phpcs:ignore

    if ('' !== allContent) {
      let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;

      if (0 < suggestionHistory.length) {
        suggestionHistory = JSON.parse(suggestionHistory);
        let childElements;

        for (let i = 0; i < allElements.length; i++) {

          let suggestionID = allElements[i].id;
          let suggestionDetail = (undefined !== suggestionHistory[suggestionID]) ? suggestionHistory[suggestionID][0] : '';
          let mode = suggestionDetail.mode;
          let action = suggestionDetail.action;
          let findItem = 'id="' + suggestionID + '"';

          if ('add' === mode?.toLowerCase()) {
            childElements = tempDiv.getElementsByClassName(addedClass);
          } else if ('edit' === mode?.toLowerCase()) {
            childElements = tempDiv.getElementsByClassName(modifiedClass);
          } else {
            childElements = tempDiv.getElementsByClassName(removedClass);
          }

          for (let j = 0; j < childElements.length; j++) {

            if (undefined !== childElements[j].id && suggestionID === childElements[j].id) {

              if ('add' === mode?.toLowerCase() || 'edit' === mode?.toLowerCase()) {
                if ('format' === action?.toLowerCase() || 'link' === action?.toLowerCase()) {
                  let currentElement = jQuery('#' + suggestionID, tempDiv)[0];
                  if (suggestionDetail['is_block_align'] || suggestionDetail['is_block_level']) {

                    childElements[j].parentNode.removeChild(childElements[j]);

                  } else if (suggestionDetail['is_element'] && suggestionDetail['newvalue'] && !suggestionDetail['is_attr_update']) {
                    jQuery('#' + suggestionID, tempDiv).replaceWith(suggestionDetail['newvalue']);  // phpcs:ignore 
                  } else if (suggestionDetail['is_attr_update']) {
                    if (currentElement.nodeName?.toLowerCase() === 'ins' ||
                      currentElement.nodeName?.toLowerCase() === 'del'
                    ) {
                      currentElement = currentElement.firstChild;
                    }
                    for (const [key, value] of Object.entries(suggestionDetail['attrs_old'])) {
                      currentElement.removeAttribute(key);
                    }

                    for (const [key, value] of Object.entries(suggestionDetail['attrs_new'])) {
                      currentElement.setAttribute(key, value);
                    }

                    jQuery('#' + suggestionID, tempDiv).replaceWith(currentElement.outerHTML);  // phpcs:ignore                
                  }
                } else {
                  jQuery('#' + suggestionID, tempDiv).replaceWith(childElements[j].innerHTML);  // phpcs:ignore 
                }

              } else if ('delete' === mode?.toLowerCase() && suggestionDetail['is_block_remove']) {
                var removeBlockclientId = jQuery('#' + suggestionID).parent().attr('data-block');
                if (removeBlockclientId) {
                  wp.data.dispatch('core/editor').editPost({ meta: { _sb_is_suggestion_mode: false } });
                  wp.data.dispatch('core/editor').removeBlock(removeBlockclientId);
                  wp.data.dispatch('core/editor').editPost({ meta: { _sb_is_suggestion_mode: true } });
                }
              } else {
                childElements[j].parentNode.removeChild(childElements[j]);
              }

              suggestionHistory[suggestionID][0]['status'] = { 'action': 'accept', 'user': userId, 'timestamp': timestamp };
              suggestionHistory[suggestionID][0]['is_draft'] = true;
              suggestionHistory[suggestionID][0]['updated_at'] = timestamp;

              let allContent = tempDiv.innerHTML;
              let updateBlockChanges = wp.data.select('core/editor').getEditedPostAttribute('meta')['_sb_update_block_changes'];

              if (0 < updateBlockChanges.length) {
                updateBlockChanges = JSON.parse(updateBlockChanges);
              }
              wp.data.dispatch('core/editor').editPost({ meta: { _sb_update_block_changes: JSON.stringify(updateBlockChanges) } });

              wp.data.dispatch('core/block-editor').resetBlocks(wp.blocks.parse(allContent));

              if(null !== document.getElementById('sg' + suggestionID)){
                document.getElementById('sg' + suggestionID)?.remove();
              }
              
              wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
              jQuery(`#cf-sg${suggestionID}`).removeClass('active');
            }
          }
        }
        this.updatePostMeta();

        jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
        jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
        jQuery('#cf-span__comments .cls-board-outer').removeClass('is-open');
      }
    }
    jQuery.event.trigger({ type: "onBlockLevelSuggestionUpdate" });
    wp.data.dispatch('core/editor').autosave();
  }

  // Updated accept suggestion with the latest code to resolve #500 @uthor - Mayank/since3.5
  acceptSuggestion() {
    const { suggestionID } = this.props;
    let { clientId } = this.props;
    const userId = wp.data.select('core').getCurrentUser().id;
    const timestamp = getTimestampWithTimezone();
    const findAttributes = window.adminLocalizer.allowed_attribute_tags;
    let content;
    let blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    if (blockAttributes === null && suggestionID !== null) {
      clientId = jQuery('#' + suggestionID).parents('.wp-block')[0]?.getAttribute('data-block');
      if(!clientId) {
        clientId = jQuery('[suggestion_id="' + suggestionID + '"], [align_sg_id="' + suggestionID + '"], [textAlign_sg_id="' + suggestionID + '"], [lock_sg_id="' + suggestionID + '"]').attr('data-block');
      }
      blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    }

    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;

    // For the block level suggestion @author Mayank Jain / since 3.4
    if (0 < suggestionHistory.length) {
      let secondaryClientId =  jQuery('[suggestion_id="' + suggestionID + '"], [align_sg_id="' + suggestionID + '"], [textAlign_sg_id="' + suggestionID + '"], [lock_sg_id="' + suggestionID + '"]').attr('data-block');
      let blockClientID = clientId;
      let blockAlignSuggestion = false, blockTextAlignSuggestion = false, blockLockSuggestion = false;
      if(secondaryClientId !== clientId){
        blockClientID = secondaryClientId;
      }

      suggestionHistory = JSON.parse(suggestionHistory);
      let suggestionDetail = suggestionHistory[suggestionID][0];

      if(suggestionDetail['is_block_align'] && blockAttributes?.align_sg_id){ // For block level align media blocks @author - Mayank/since 3.6
        blockAlignSuggestion = true;
      }
      if(suggestionDetail['is_text_align'] && blockAttributes?.textAlign_sg_id){ // For block level text align media blocks @author - Mayank/since 3.6
        blockTextAlignSuggestion = true;
      }
      if(suggestionDetail['is_block_lock'] && blockAttributes?.lock_sg_id){ // For block level lock media blocks @author - Mayank/since 3.6
        blockLockSuggestion = true;
      }
      if(suggestionDetail && (suggestionDetail['is_block_added'] || suggestionDetail['is_block_remove'] || blockAlignSuggestion || blockTextAlignSuggestion || blockLockSuggestion)){
        let mode = suggestionHistory[suggestionID][0].mode;
        let action = suggestionHistory[suggestionID][0].action;

        if ('add' === mode.toLowerCase() || 'delete' === mode.toLowerCase() || 'edit' === mode.toLowerCase()) {
          if(suggestionDetail['is_block_added']){
            document.getElementById('sg' + suggestionID)?.remove();
            const element = document.querySelector(`[suggestion_id="${suggestionID}"]`);
            if(element){
              element.classList.remove("mdadded", "blockAdded");
              element.removeAttribute("suggestion_id");
              element.removeAttribute("data-sgblockadded");
              
              const attributes = wp.data.select('core/block-editor').getBlockAttributes(blockClientID);
              attributes.className = attributes.className.replace(/(mdadded|blockAdded)\s*/g, '');
              wp.data.dispatch('core/editor').updateBlockAttributes(blockClientID, {
                className: attributes.className,
                suggestion_id: null,
              });
              const hideSuggestionIcon = document.querySelectorAll('[data-blockclient_id="'+blockClientID+'"]');
              if(hideSuggestionIcon){
                hideSuggestionIcon.forEach(element => {
                  if (!element.hasAttribute('datatext')) {
                    element.style.display = 'none';
                  }
                  element.classList.remove('focus');
                });
              }
              
              wp.data.dispatch('core/block-editor').selectBlock(blockClientID);
            }
          }
          if(suggestionDetail['is_block_remove']){
            document.getElementById('sg' + suggestionID)?.remove();
            const element = document.querySelector(`[suggestion_id="${suggestionID}"]`);
            if(element) {
              element.classList.remove("mdremoved", "blockremove"); // Removed block remove class @author - Mayank / since 3.5
              element.removeAttribute("suggestion_id");
              element.removeAttribute("data-sgremove");
              element.classList.add('removedBlock');

              const attributes = wp.data.select('core/block-editor').getBlockAttributes(blockClientID);
              attributes.className = attributes.className.replace(/(mdremoved|blockremove)\s*/g, 'removedBlock'); // Removed block remove class @author - Mayank / since 3.5

              wp.data.dispatch('core/editor').updateBlockAttributes(blockClientID, {
                className: attributes.className,
                suggestion_id: null,
              });
              
              // Delete all the suggestions if block deleted @Author - Mayank/ since 3.4
              const parentElement = document.getElementById('block-'+blockClientID);
              if(parentElement){
                const childElements = parentElement.querySelectorAll('ins, del');
                if(childElements){
                  const childIds = Array.from(childElements).map(element => 'sg' + element.getAttribute('id'));
                  if(childIds){
                    childIds.forEach(childId => {
                      const element = document.getElementById(childId);
                      if (element) {
                        element.remove();
                      }
                    });
                  }
                }
              }
              
              // Delete all the comments if block deleted @Author - Mayank/ since 3.4
              var commentsIds = $('#block-'+blockClientID + ', #block-'+blockClientID + ' *').map(function() {
                return $(this).attr('datatext');
              }).get();
              if(commentsIds){
                commentsIds.forEach(function(commentsId) {
                  const matchingIds = $(`[datatext=${commentsId}]`);
                  if (matchingIds) {
                    $('#'+commentsId).remove(); // remove the element with the sid
                  }
                });
              }
              wp.data.dispatch('core/block-editor').removeBlock(blockClientID);
            }
          }
          if(blockAlignSuggestion){ // For block align suggestion media blocks @author - Mayank/since 3.6
            let alignValue = suggestionDetail['newvalue'] || '';
            wp.data.dispatch('core/editor').updateBlockAttributes(blockClientID, {
              align: alignValue,
              align_sg_id: null,
            });
          }
          if(blockTextAlignSuggestion){ // For block text align suggestion media blocks @author - Mayank/since 3.6
            let textAlignValue = suggestionDetail['newvalue'] || '';
            wp.data.dispatch('core/editor').updateBlockAttributes(blockClientID, {
              textAlign: textAlignValue,
              textAlign_sg_id: null,
            });
          }
          if(blockLockSuggestion){ // For block lock suggestion media blocks @author - Mayank/since 3.6
            wp.data.dispatch('core/editor').updateBlockAttributes(blockClientID, {
              lock: suggestionDetail['newvalue'],
              lock_sg_id: null,
            });
          }

          // save status for activity center log
          suggestionHistory[suggestionID][0]['status'] = { 'action': 'accept', 'user': userId, 'timestamp': timestamp };
          suggestionHistory[suggestionID][0]['is_draft'] = true;
          suggestionHistory[suggestionID][0]['updated_at'] = timestamp;
          this.updateBlockContentChanges(blockClientID);
          wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
          this.updatePostMeta();
          wp.data.dispatch('core/editor').savePost(); // To resolve #493 issue @author - Mayank / since 3.5
          jQuery(`#cf-sg${suggestionID}`).removeClass('active');
          jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
          jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
          jQuery('#cf-span__comments .cls-board-outer').removeClass('is-open');
        }
        return;
      }
    }
    if(blockAttributes){
      for (const [key, value] of Object.entries(blockAttributes)) {
        if(findAttributes.includes(key)){
          content = blockAttributes[key];
          if ('' !== content) {
            let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;
            if (0 < suggestionHistory.length) {
              suggestionHistory = JSON.parse(suggestionHistory);
              let findItem = 'id="' + suggestionID + '"';
              if (suggestionHistory[suggestionID] && -1 !== content.indexOf(findItem)) {
                let suggestionDetail = suggestionHistory[suggestionID][0];
                let mode = suggestionHistory[suggestionID][0].mode;
                let action = suggestionHistory[suggestionID][0].action;
                let tempDiv = document.createElement('div');
                tempDiv.innerHTML = content;  // phpcs:ignore
                let childElements;
      
                if ('add' === mode.toLowerCase()) {
                  childElements = tempDiv.getElementsByClassName(addedClass);
                } else if ('edit' === mode.toLowerCase()) {
                  childElements = tempDiv.getElementsByClassName(modifiedClass);
                } else {
                  childElements = tempDiv.getElementsByClassName(removedClass);
                }
      
                for (let i = 0; i < childElements.length; i++) {
                  if (undefined !== childElements[i].id && suggestionID === childElements[i].id) {

                    // remove comment board over suggestion if we reject the suggestion. @author: Rishi Shah.
                    var childCommentId = jQuery('#' + suggestionID).find('.mdspan-comment').attr( 'datatext' );
                    var childSuggesionId = jQuery('#' + suggestionID).find('.mdmodified').attr( 'id' );
                    if( childElements[i].classList.contains('mdremoved') ) {
                      setTimeout(function () { 
                        if( '' !== childCommentId && undefined !== childCommentId ) {
                          removeTag( childCommentId ); // eslint-disable-line
                          jQuery('#' + childCommentId).remove();
                        } else if( '' !== childSuggesionId ) {
                          jQuery('#sg' + childSuggesionId).remove();
                        }
                      }, 500);
                    }

                    // Added for lock feature functionality @author - Mayank / since 3.5
                    if(suggestionDetail['is_block_lock'] ) {
                      wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { lock: suggestionDetail['newvalue'] });
                      childElements[i].parentNode.removeChild(childElements[i]);
                    }
                    if(suggestionDetail['is_text_align']){ // Added for text align functionality @author - Mayank / since 3.5
                      if (suggestionDetail['newvalue'] !== undefined || suggestionDetail['newvalue'] !== '') {
                        wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { textAlign: suggestionDetail['newvalue']});
                      } else {
                        wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { textAlign: ''});
                      }
                    }
                    if (suggestionDetail['is_block_align']){
                      if (suggestionDetail['newvalue'] !== undefined || suggestionDetail['newvalue'] !== '') {
                        wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { align: suggestionDetail['newvalue']});
                      } else {
                        wp.data.dispatch('core/block-editor').updateBlockAttributes(clientId, { align: ''});
                      }
                    }

                    if ('add' === mode.toLowerCase() || 'edit' === mode.toLowerCase()) {
                      if ('format' === action.toLowerCase() || 'link' === action.toLowerCase()) {
                        let currentElement = jQuery('#' + suggestionID, tempDiv)[0];

                        if (suggestionDetail['is_block_align'] || suggestionDetail['is_block_level'] ) {
                          if(childElements[i]){ // Added condition to prevent from any error/warning @author - Mayank / since 3.5
                            childElements[i].parentNode.removeChild(childElements[i]);
                          }
      
                        } else if(undefined !== currentElement && null !== currentElement?.innerHTML.match(/<mdspan/g) ) {
                          jQuery('#' + suggestionID, tempDiv).replaceWith(currentElement?.firstChild.outerHTML);
                        } else if (suggestionDetail['is_element'] && suggestionDetail['newvalue'] && !suggestionDetail['is_attr_update'] && null === currentElement.innerHTML.match(/<mdspan/g)) {
                          jQuery('#' + suggestionID, tempDiv).replaceWith(suggestionDetail['newvalue']);  // phpcs:ignore 
                        } else if (suggestionDetail['is_attr_update']) {
                          if (currentElement.nodeName.toLowerCase() === 'ins' ||
                            currentElement.nodeName.toLowerCase() === 'del'
                          ) {
                            currentElement = currentElement?.firstChild;
                          }
                          for (const [key, value] of Object.entries(suggestionDetail['attrs_old'])) {
                            currentElement.removeAttribute(key);
                          }
      
                          for (const [key, value] of Object.entries(suggestionDetail['attrs_new'])) {
                            currentElement.setAttribute(key, value);
                          }
      
                          jQuery('#' + suggestionID, tempDiv).replaceWith(currentElement.outerHTML);  // phpcs:ignore                
                        }
                      } else {
                        jQuery('#' + suggestionID, tempDiv).replaceWith(childElements[i].innerHTML);  // phpcs:ignore 
                      }
                    } else {
                      childElements[i].parentNode.removeChild(childElements[i]);
                    }
      
                    // save status for activity center log
                    suggestionHistory[suggestionID][0]['status'] = { 'action': 'accept', 'user': userId, 'timestamp': timestamp };
                    suggestionHistory[suggestionID][0]['is_draft'] = true;
                    suggestionHistory[suggestionID][0]['updated_at'] = timestamp;
      
                    let finalContent = tempDiv.innerHTML;
                    this.updateBlockContentChanges(clientId);
                    
                    wp.data.dispatch('core/block-editor').updateBlock(clientId, {
                      attributes: {
                        [key]: finalContent
                      }
                    });
                    
                    document.getElementById('sg' + suggestionID)?.remove();
                    wp.data.dispatch('core/editor').editPost({ meta: { _sb_suggestion_history: JSON.stringify(suggestionHistory) } });
                    this.updatePostMeta();
                    wp.data.dispatch('core/editor').savePost(); // To resolve #493 issue @author - Mayank / since 3.5
                    jQuery(`#cf-sg${suggestionID}`).removeClass('active');
                    jQuery('#cf-span__comments .cls-board-outer').removeAttr('style');
                    jQuery('#cf-span__comments .cls-board-outer').removeClass('focus');
                    jQuery('#cf-span__comments .cls-board-outer').removeClass('is-open');
      
                    break;
                  }
                }

              }
            }
          }
        }
      }
    }   
    jQuery.event.trigger({ type: "onBlockLevelSuggestionUpdate" });
    wp.data.dispatch('core/editor').autosave();
  }

  updatePostMeta() {
    // update post suggestion history meta on autosave
    var isAutosavingPost = wp.data.select('core/editor').isAutosavingPost();
    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;

    if (!isAutosavingPost && suggestionHistory !== undefined) {

      var data = {
        'action': 'sg_update_suggestion_history',
        'currentPostID': wp.data.select('core/editor').getCurrentPost().id,
        'suggestionHistory': wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history,
        nonce: adminLocalizer.nonce,
      };

      jQuery.post(ajaxurl, data, function (response) { // eslint-disable-line  

        let suggestionHistoryUpdated = wp.data.select('core/editor').getCurrentPostAttribute('meta')['_sb_suggestion_history'];
        suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')?._sb_suggestion_history;

        // merge current post meta and edited post meta
        if (suggestionHistoryUpdated !== '' && suggestionHistory !== '') {
          suggestionHistoryUpdated = _merge(JSON.parse(suggestionHistory), JSON.parse(suggestionHistoryUpdated));
          suggestionHistoryUpdated = JSON.stringify(suggestionHistoryUpdated);
        } else {
          suggestionHistoryUpdated = suggestionHistory;
        }

        // update post suggestions meta value, 
        // it send mail multiple times, if we dont update is_draft status in edited meta
        wp.data.dispatch('core/editor').editPost({
          meta: { _sb_suggestion_history: suggestionHistoryUpdated },
        });

      });

    }
  }

  componentDidMount() {
    let localUser = localStorage.getItem('sg_userid');
    if (this.state.currentUserid !== this.props.userID && parseInt(localUser) !== this.props.userID) {


      wp.apiFetch({ path: '/cf/v2/getuserdata/?userid=' + this.props.userID }).then(userData => {

        if (userData && Object.keys(userData).length > 0) {

          localStorage.setItem("sg_userid", userData.id);
          localStorage.setItem("sg_username", userData.username);
          localStorage.setItem("sg_role", userData.userRole);
          localStorage.setItem("sg_avtarUrl", userData.avatarUrl);

          this.setState({
            isLoaded: true
          });

        }
      });

    } else {
      this.setState({
        isLoaded: true
      });
    }
  }

  renderNormalMode() {
    const { action, userID, index, timestamp } = this.props;
    const { isLoaded } = this.state;
    // Display the textarea for new comments.
    jQuery('.cls-board-outer.focus .shareCommentContainer').show();

    let owner = wp.data.select("core").getCurrentUser().id;
    let commentVisibility = 'block';
    if (index > getCommentsLimit()) {
      commentVisibility = 'none';
    }

    let str = this.props.title;
    let readmoreStr = '';
    const maxLength = 300;
    if (maxLength < str.replace(/<\/?[^>]+(>|$)/g, "").length) {
      readmoreStr = str;
      str = str.substring(0, maxLength) + '...';
    }
    // Removing contenteditable attr from the link.
    str = str.replace(/contenteditable=\"false\"/ig, 'data-edit="false"'); // eslint-disable-line
    str = translateStringFormat(str);

    let Datetime = convertedDatetime(timestamp);
    let timeAgoDate = timeAgo(timestamp);

    if (this.state.currentUserid !== this.props.userID && !isLoaded) {
      return <div></div>;
    } else {

      let username, avtarUrl, role;
      if (this.state.currentUserid !== userID) {
        username = localStorage.getItem("sg_username");
        avtarUrl = localStorage.getItem("sg_avtarUrl");
        role = localStorage.getItem("sg_role");
      } else {
        let userinfo = getCurrentUserInfoById();
        username = userinfo['username'];
        avtarUrl = userinfo['avtarUrl'];
        role = userinfo['role'];
      }

      return (
        <div className="commentContainer" style={{ display: commentVisibility }} id={timestamp}>
          <div className="commentInnerContainer">
            <div className="comment-header">
              <div className="comment-details">
                <div className="avatar"><img src={avtarUrl} alt="avatar" /></div>
                <div className="commenter-name-time">
                  <div className="commenter-name">{username.charAt(0).toUpperCase() + username.slice(1)} <span className="tooltip">{role}</span></div>
                  <div className="comment-time"><span className="comment-time-wrapper">{timeAgoDate} <span className="tooltip">{Datetime}</span></span></div>
                </div>
                <div className="comment-actions">

                  {index === 0 && "guest" !== currentUserData.role &&
                    <ul className="suggestion-comment-accpt-meta">
                      <li onClick={this.acceptSuggestion.bind(this)}>
                        <i className="btn-add-suggestion dashicons dashicons-yes"><span className="tooltip">{__('Accept suggestion', 'content-collaboration-inline-commenting')}</span></i>
                      </li>
                      <li onClick={this.removeSuggestion.bind(this)}>
                        <i className="btn-remove-suggestion dashicons dashicons-no-alt"><span className="tooltip">{__('Reject suggestion', 'content-collaboration-inline-commenting')}</span></i>
                      </li>

                    </ul>
                  }


                  {index === 0 &&
                    <React.Fragment>
                      <div className="buttons-wrapper">
                        <span className="tooltip">{__('More Options...', 'content-collaboration-inline-commenting')}</span>
                        <div className="more-option-btn"></div>
                        <div className="comment-more-option">
                          <ul className="comment-more-option-list">

                            <li onClick={this.copy.bind(this)}>
                              <i className="dashicons dashicons-share btn btn-comment" title={__('Copy Link', 'content-collaboration-inline-commenting')}></i><span className="more-option-meta">{__('Share', 'content-collaboration-inline-commenting')}</span>
                            </li>
                            <React.Fragment>
                              {userID === owner &&
                                <li className="js-trash-suggestion" >
                                  <i className="dashicons dashicons-trash btn btn-comment "></i><span className="more-option-meta">{__('Delete', 'content-collaboration-inline-commenting')}</span>
                                </li>
                              }
                            </React.Fragment>

                          </ul>
                        </div>
                      </div>

                    </React.Fragment>
                  }

                  {'' !== this.state.copySuccess &&
                    <p className="comment-copied-tooltip">{this.state.copySuccess}</p>
                  }
                  {'reply' === action && userID === owner &&
                    <div className="buttons-wrapper">
                      <span className="tooltip">{__('More Options...', 'content-collaboration-inline-commenting')}</span>
                      <div className="more-option-btn"></div>
                      <div className="comment-more-option">
                        <ul className="comment-more-option-list">
                          <li onClick={this.editComment}>
                            <i className="dashicons dashicons-edit btn btn-comment js-edit-comment"></i><span className="more-option-meta">{__('Edit', 'content-collaboration-inline-commenting')}</span>
                          </li>
                          <li className="js-trash-comment">
                            <i className="dashicons dashicons-trash btn btn-comment "></i><span className="more-option-meta">{__('Delete', 'content-collaboration-inline-commenting')}</span>
                          </li>
                        </ul>
                      </div>
                    </div>
                  }
                </div>
              </div>
            </div>
            <div className="commentText">
              <span className='readlessTxt readMoreSpan active' >{this.props.mode.toLowerCase() !== 'reply' && <strong>{__(this.props.action, 'content-collaboration-inline-commenting')} :</strong>} {renderHTML(__(str, 'content-collaboration-inline-commenting'))} {'' !== readmoreStr && <span className='readmoreComment'>{__('show more', 'content-collaboration-inline-commenting')}</span>}</span>
              <span className='readmoreTxt readMoreSpan'>{this.props.mode.toLowerCase() !== 'reply' && <strong>{__(this.props.action, 'content-collaboration-inline-commenting')} :</strong>} {renderHTML(readmoreStr)} {'' !== readmoreStr && <span className='readlessComment'>{__('show less', 'content-collaboration-inline-commenting')}</span>}</span>

            </div>

            <div className="comment-delete-overlay">
              <span className="comment-overlay-text">{__('Delete this comment?', 'content-collaboration-inline-commenting')}</span>
              <div className="comment-delete-overlay-inner">
                <button onClick={index === 0 ? this.removeSuggestion.bind(this) : this.removeComment.bind(this)} className="btn btn-delete">{__('Delete', 'content-collaboration-inline-commenting')} </button>
                <button onClick={this.cancelEdit.bind(this)} className="btn btn-cancel">{__('Cancel', 'content-collaboration-inline-commenting')}</button>
              </div>
            </div>
            {undefined !== this.props.editedTime &&
              <time className="updated-time"> {sprintf(__('edited at %s', 'content-collaboration-inline-commenting'), timeAgo(this.props.editedTime))} <span className="tooltip">{convertedDatetime(this.props.editedTime)}</span></time>
            }

          </div>
        </div>
      );
    }
  }

  renderEditingMode() {
    const { timestamp } = this.props;
    const { isLoaded } = this.state;
    var previousValue = this.props.children[0];
    // Filtering anchor tag and return the url text only.
    // For bug fixing of semicolon there is a little chnage in regex
    previousValue = removeLinkFromEditableText(previousValue);
    previousValue = filterTextForEdit(previousValue);

    var newText = this.state.contentHtml;
    newText = filterTextForEdit(newText);

    let buttonClass = '';
    if (newText === '' || newText === previousValue) {
      buttonClass = 'btn-disabled';
    }

    //set attachment state - code added by meet
    if ('' != this.state.mediaId) {
      buttonClass = '';
    }

    // Hide the textarea for new comments.
    jQuery('.cls-board-outer.focus .shareCommentContainer').hide();

    let Datetime = convertedDatetime(timestamp);
    let timeAgoDate = timeAgo(timestamp);

    if (this.state.currentUserid !== this.props.userID && !isLoaded) {
      return <div></div>;
    } else {

      let username, avtarUrl, role;
      if (this.state.currentUserid !== this.props.userID) {
        username = localStorage.getItem("sg_username");
        avtarUrl = localStorage.getItem("sg_avtarUrl");
        role = localStorage.getItem("sg_role");
      } else {
        let userinfo = getCurrentUserInfoById();
        username = userinfo['username'];
        avtarUrl = userinfo['avtarUrl'];
        role = userinfo['role'];
      }
      return (
        <div className="commentContainer " id={timestamp}>
          <div className="commentInnerContainer">
            <div className="comment-header">
              <div className="comment-details">
                <div className="avatar"><img src={avtarUrl} alt="avatar" /></div>
                <div className="commenter-name-time">
                  <div className="commenter-name">{username.charAt(0).toUpperCase() + username.slice(1)} <span className="tooltip">{role}</span></div>
                  <div className="comment-time">{timeAgoDate} <span className="tooltip">{Datetime}</span></div>
                </div>
              </div>
            </div>
            <div className="commentText">
              <div className="cf-share-comment-wrapper js-cf-share-comment-wrapper">
                <ContentEditable
                  innerRef={this.contentEditable}
                  html={this.state.contentHtml}
                  disabled={false}
                  onChange={(e) => this.setState({ contentHtml: e.target.value })}
                  id={`edit-${this.props.timestamp}`}
                  className="cf-share-comment js-cf-edit-comment"
                  placeholder={__('Edit your comments...', 'content-collaboration-inline-commenting')}

                />
              </div>
              {undefined !== this.state.mediaId && '' !== this.state.mediaId &&
                <React.Fragment>
                  <div className="cf-commentboard-attach-wrap">
                    <div id="cf_thumb_file">
                      <a href={this.state.mediaUrl} target="_blank">{this.state.mediaName}</a>
                    </div>
                    <button onClick={this.deleteAttachment.bind(this)} className="btn-attchment-del">
                      {icons.attachment_delete}
                      <span className="tooltip">{__('Delete attachment', 'content-collaboration-inline-commenting')}</span>
                    </button>
                  </div>
                </React.Fragment>
              }
            </div>
            <div className="comment-delete-overlay">
              <span className="comment-overlay-text">{__('Delete this comment?', 'content-collaboration-inline-commenting')}</span>
              <div className="comment-delete-overlay-inner">
                <button onClick={this.removeComment.bind(this)} className="btn btn-delete">{__('Delete', 'content-collaboration-inline-commenting')}</button>
                <button onClick={this.cancelEdit.bind(this)} className="btn btn-cancel">{__('Cancel', 'content-collaboration-inline-commenting')}</button>
              </div>
            </div>
            <button onClick={this.saveComment.bind(this)} className={`btn-comment save-btn ${buttonClass}`} >
              {__('Save', 'content-collaboration-inline-commenting')}
              <span className="tooltip">{__('Save Changes', 'content-collaboration-inline-commenting')}</span>
            </button>
            <button onClick={this.cancelEdit.bind(this)} className="btn-comment js-cancel-comment">
              {__('Cancel', 'content-collaboration-inline-commenting')}
              <span className="tooltip">{__('Discard Changes', 'content-collaboration-inline-commenting')}</span>
            </button>
          </div>
        </div>
      );
    }
  }

  render() {
    return this.state.editing ? this.renderEditingMode() : this.renderNormalMode();
  }
}
